  cursor: pointer;
  transform: rotate(270deg);
  position: fixed;
  bottom: 0;
  right: 0;
  background-color: #ddd;
  color: #555;
  text-align: center;
  font-size: 30px;
  text-decoration: none;
}
#back2Top:hover {
  background-color: #ddf;
  color: #f00;
}
blockquote {
  border-left: 0.25rem solid #3eb0ef;
  padding-left: 2rem;
  font-style: italic;
}
#overlaytrigger {
  font-size: 30px;
}
#overlaytrigger:hover {
  cursor: pointer;
  color: #e51843;
}
.left-default-column {
