}
.overlay {
  height: 100%;
  width: 100%;
  display: none;
  position: fixed;
  z-index: 9999;
  top: 0;
  left: 0;
  background-color: #000;
  background-color: rgba(0,0,1,0.9); /* Black with a little bit see-through */
}
.overlay-content {
  position: relative;
  top: 18%;
  width: 80%;
  text-align: center;
  margin-top: 30px;
  margin: auto;
}
.overlay .closebtn {
  position: absolute;
  top: 20px;
  right: 45px;
  font-size: 60px;
  cursor: pointer;
  color: #fff;
}
.overlay .closebtn:hover {
  color: #ccc;
}
.overlay input[type=text] {
  color: #fff;
  height: 5rem;
  font-size: 2.25rem;
  padding: 1.25rem 0;
  font-weight: 900;
  background-color: transparent;
  box-shadow: none;
  border: 0;
  border-bottom: 0.1875rem solid #fff;
  font-family: "Montserrat", sans-serif;
}
.overlay input[type=text]:focus {
  outline: none;
}
.overlay button {
  float: left;
  width: 20%;
  padding: 15px;
  background: #ddd;
  font-size: 17px;
  border: none;
  cursor: pointer;
}
.overlay button:hover {
  background: #bbb;
}
@media (max-width: 767.98px) {
  .left-default-column {
    display: none;
  }
  .right-default-column .feature-aside {
    margin-top: 10px;
  }
  .right-default-column .navigation-container .navigation-link {
    text-align: center !important;
  }
  .right-default-column .div-wapper .newer-post {
    margin-bottom: 20px;
  }
}
@media (max-height: 600px) {
  .left-default-column .profile-image,
  .left-default-column .quote {
    display: none;
  }
}
.utterances {
  max-width: 100%;
}
